*{
  padding: 0;
  margin: 0;
}
.App{
  background-color: #435334;
  height: 100vh;
  /* color:#9EB384 */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
/* white box which will contain text-box */
.input-container{
  width: 600px;
  height: 350px;
  background-color: white;
  border-radius: 10px;
  padding: 20px;
}
.input-box{
  width: 600px;
  height: 280px;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
}
.text-box{
  resize: none;
  width: 278px;
  height: 200px;
  border-radius: 5px;
  padding: 10px;
  font-family: 'Times New Roman', Times, serif;
  font-size: 15px;
}
/* translate button style */
/* volume icon styling */
.icon{
  color:#435334;
  width: 30px;
  margin: 5px 8px;
}
/* translate btn and exchange icon styling */
.btn{
  height: 40px;
  margin: 20px 0px;
  width: 100%;
  padding: 6px 0;
  background-color: #435334;
  color:white;
  border:none;
  border-radius: 5px;
}
.btn:hover{
  background-color: #7a8b66;
}
.icon:hover{
  color:#7a8b66;
}
.select-lang{
  height: 30px;
}